<template>
  <div>
    <div v-for="(row, rowIndex) in imageRows" :key="rowIndex" class="image-row">
      <img
        v-for="(image, index) in row"
        :key="index"
        :src="image.src"
        @click="showImg(rowIndex * perRow + index)"
        class="image-thumbnail"
      />
    </div>
    <button @click="loadMore" :disabled="loading">Carregar mais</button>
    <vue-easy-lightbox :visible="visibleRef" :imgs="images" :index="indexRef" @hide="onHide" />
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'
import VueEasyLightbox from 'vue-easy-lightbox'

const images = ref<Array<{ src: string }>>([])
const perRow = 6
const rows = ref(2)
const loading = ref(false)
const visibleRef = ref(false)
const indexRef = ref(0)

const fetchImages = async () => {
  try {
    const response = await fetch('/api/portifolio')
    const data = await response.json() as string[]
    images.value = data.map((file: string) => ({ src: `/img/portifolio/${file}` }))
  } catch (error) {
    console.error('Error fetching images:', error)
  }
}

onMounted(() => {
  fetchImages()
})

const imageRows = computed(() => {
  const rowsArr = []
  for (let i = 0; i < rows.value; i++) {
    rowsArr.push(images.value.slice(i * perRow, (i + 1) * perRow))
  }
  return rowsArr
})

const loadMore = async () => {
  loading.value = true
  await new Promise(resolve => setTimeout(resolve, 1000)) // Simulate loading delay
  rows.value += 1
  loading.value = false
}

const showImg = (index: number) => {
  indexRef.value = index
  visibleRef.value = true
}

const onHide = () => {
  visibleRef.value = false
}
</script>

<script lang="ts">
export default {
  name: 'Portifolio',
  components: {
    VueEasyLightbox
  }
}
</script>

<style scoped>
.image-row {
  display: flex;
  justify-content: space-between;
}
.image-thumbnail {
  width: 100px;
  height: 100px;
  object-fit: cover;
  cursor: pointer;
}
</style>
